@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
:root {
  --primary-blue  : #546ED2;
  --secondary-blue: #1F3E72;
  --light-blue    : #EAF1FE;
  --yellow-color  : #FFD012;
  --gray-color    : #6D7B93;
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
}
*, *:before, *:after {
  box-sizing: inherit;
}


* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  text-transform: capitalize;
  transition: all .3s ease;
}
body{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

a {
  color: unset;
  text-decoration: none;
}

.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 10%;

    background: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(.4rem);
    -webkit-backdrop-filter: blur(.4rem);
    border: .1rem solid rgba(255, 255, 255, 0.18);
    position: fixed;
    z-index: 2;
}
.app__navbar-logo  {
    flex: 0.25 1;

}

.app__navbar-logo a h1 {
    font-size: 2.8rem;
    font-weight: bold;
    color: var(--primary-blue);
}
.app__navbar-logo a:hover h1 {
    color: var(--secondary-blue);
}
.app__navbar-content{
    flex: 1 1;
    width: 100%;
    justify-content: space-around;
    display: flex;align-items: center;
}
.app__navbar-content button {
    width: 20%;
}
.app__navbar-links {
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: flex-end;
    grid-gap: 8rem;
    gap: 8rem;
    margin-right: 10rem;
}
.app__navbar-links li{
    font-weight: bold;
    font-size: 1.8rem;
    border-bottom: .1rem solid transparent;
}
.app__navbar-links li:hover {
    border-bottom: .1rem solid var(--secondary-blue);

}

.app__navbar-movil {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.app__navbar-menu{
    position: relative;
    width: 3.5rem;
    height: 3.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--primary-blue);
}
.app__navbar-menu svg {
    width: 70%;
    height: 70%;
    color: #fff;
}
.app__navbar-menu div {
    position: fixed;
    top: 0; bottom: 0;
    right: 0;
    width: 60%;
    height: 100vh;
    padding: 1rem;
    z-index: 5;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    background: #fff;
    box-shadow: 0 0 2rem rgba(168, 168, 168, 0.15);
    
}
.app__navbar-menu div svg {
    width: 3.5rem;
    height: 3.5rem;
    color: var(--secondary-blue);
}
.app__navbar-menu div button {
    width: 100%;
}

.app__navbar-menu div ul{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    height: 90%;
    list-style: none;
    
}
.app__navbar-menu div ul li {
    margin: 1rem;
    font-size: 1.8rem;
    font-weight: bold;
    border-bottom: .1rem solid transparent;
}
.app__navbar-menu div ul li:hover {
    border-bottom: .1rem solid var(--secondary-blue);
}
/*Media queries*/
@media (max-width: 990px) {
    .app__navbar{
        padding: 2rem 5%;
    }
    .app__navbar-content {
        display: none;
    }
}
@media (min-width: 990px) {
    .app__navbar-movil{
        display: none;
    }
}
.app__footer{
    max-width: 100%;
    margin-top: 10rem;
    background-color: var(--light-blue);
    padding: 5rem 10%;
    display: flex;
    justify-content: space-between;
}

.app__footer-info{
    display: flex;
}
.app__footer-info{
    font-size: 2rem;
    font-weight: bold;
    color: var(--secondary-blue);
}
.app__footer-info p{
    font-size: 1.8rem;
    font-weight: 500;
    color: var(--gray-color);
    margin: 2rem 0;
    cursor: pointer;
}
@media (max-width:490px) {
    .app__footer{
        flex-direction: column;
    }
    .app__footer-info{
        margin-top: 2rem;
    }
}
.app__primaryBtn{
    padding: 1.5rem 3rem;
    border: .1rem solid transparent;
    cursor: pointer;
    color: #fff;
    font-size: 1.8rem;
    font-weight: bold;
    border-radius: .5rem;
    background-color: var(--primary-blue);
    box-shadow: 0px 2px 5px 4px rgba(0,0,0,0.2);
    -webkit-box-shadow: 0px 2px 5px 4px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 2px 5px 4px rgba(0,0,0,0.2);
}

.app__primaryBtn:hover {
    color: var(--primary-blue);
    border: .1rem solid var(--primary-blue);
    background-color: #fff;
}
.heading-title{
    font-size: 4.7rem;
    font-weight: bold;
    margin: 5rem 0;
    color: var(--secondary-blue);

}
.app__about{
    padding: 0 10%;
    display: flex;
    flex-direction: column!important;

}

.app__about-logos{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 5rem 0;
}
.app__about-logos .logo{
    width: 25rem;
    height: 20rem;
    display: flex;
    align-items: center;
    justify-content: center;

}
.app__about-logos .logo img {
    max-width: 100% ;
    height: auto;
    object-fit: cover;
    opacity: .25;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}

.app__about-logos .logo:hover img {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}

.app__about-title{
    width: 40%;
    letter-spacing: .1rem;
    font-size: 2rem;
    color: var(--gray-color);
    font-weight: 500;
}
.app__about-boxes{
    margin: 5rem 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
    grid-gap: 1rem;
    gap: 1rem;
    place-items: center;
}
.box{
    max-width: 30rem;
    height: 35rem;
    padding: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    grid-gap: 3rem;
    gap: 3rem;
    border-radius: .5rem;
    border: 1px solid var(--light-blue);
}
.box:hover {
    background: rgba( 255, 255, 255, 0.9 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7px );
    -webkit-backdrop-filter: blur( 7px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.box .icon-container{
    width: 4rem;
    height: 4rem;
}
.box h3 {

    font-size: 2.4rem;
    font-weight: bold;
    color: var(--secondary-blue);
}
.box p{
    color: var(--gray-color);
    font-weight: 500;
    letter-spacing: 2px;
    font-size: 1.8rem;
}
/* media queries */
@media (max-width: 460px) {
    .app__about-title{
        width: 100%;
    }
    .app__about-logos .logo{
        width: 100%;
        height: 15rem;
    }
}
.app__contact{
    padding-left: 10%;
    align-items: center;
}

.app__contact-info{
    flex: 1 1;
    width: 100%;
    padding-right: 10%;
    align-self: flex-start;
}
.app__contact-info p.p-text {
    font-weight: 500;
    font-size: 2rem;
}
.app__contact-img{
    flex: 1 1;
    width: 100%;
    align-self: flex-end;
}

.app__contact-img img{
    max-width: 100%;
    height: 100%;
    object-fit: cover;
}

.app__contact-info_types{
    margin-top: 10rem;
    max-width: 100%;
    grid-gap: 5rem;
    gap: 5rem;
    display: grid;
    place-items: center;
    grid-template-columns: repeat(2, 1fr);
}

.box-container{
    width: 25rem;
    height: 20rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    border: .3rem solid var(--light-blue);
    border-radius: 1rem;
}
.box-container:hover{
    background: rgba( 255, 255, 255, 0.9 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7px );
    -webkit-backdrop-filter: blur( 7px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.box-info{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.box-info .icon-container {
    width: 5rem;
    height: 5rem;
    
}
.box-info .icon-container svg{
    width: 60%;
    height: 60%;
}

@media (max-width: 490px) {
    .app__contact-info_types{
        max-width: 90%;
        grid-template-columns: 1fr;
        margin: 2rem auto;
    }
    .app__contact{
        flex-direction: column!important;
        padding-left: 5%;
    }
}
.app__header{
    display: flex;
    align-items: center;
    height: 100%;
}

.app__header-text{
    flex: 1 1;
    width: 100%;
    height: 100%;
    padding: 2rem 10%;
    margin-top: 6rem;
}
.app__header-text h2{
    font-size: 10rem;
    color: var(--secondary-blue);
    margin-bottom: 5rem;
}
.app__header-text p.p-text {
    font-weight: 500;
    font-size: 2.8rem;
    margin:5rem 0;
    width: 80%;
}
.app__header-img{
    flex: 1 1;
    width: 100%;
    height: 100%;
}
.app__header-info{
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 5rem;
    gap: 5rem;
}
.app__header-info div {
    display: flex;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}
.app__header-info div svg {
    font-size: 2rem;
    color: var(--yellow-color);
}
.app__header-info div p{
    font-size: 2rem;
    font-weight: bold;
    color: var(--secondary-blue);
}
.app__header-img img{
    max-width: 100%;
    height: 100vh;
    object-fit: cover;
}
.app__header-schedule{
    padding: 10rem 0;
}
.app__header-schedule-title{
    display: flex;
    align-items: center;
    margin-bottom: 5rem;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}
.app__header-schedule-title h3{
    font-weight: bold;
    color: var(--secondary-blue);
}
.app__header-schedule-info{
    margin-top: 2rem;
    padding: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: rgba( 255, 255, 255, 0.85 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7.5px );
    -webkit-backdrop-filter: blur( 7.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.schedule-info-container {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    grid-gap: 1rem;
    gap: 1rem;
}

.app__header-schedule-info .schedule-info-container .info{
    display: flex;
    flex-direction: column;
}
.schedule-info-container .info p:first-child {
    color: var(--gray-color);
    font-weight: 500;
    font-size: 2.2rem;
}
.schedule-info-container .info p:last-child {
    color: var(--secondary-blue);
    font-weight: bold;
    font-size: 2.4rem;
}

.app__header-schedule-info button {
    margin-left: 3rem;
}
/* media queries */

@media (max-width:990px) {
    .app__header-schedule-info{
        flex-direction: column;
        align-items: flex-start;
        grid-gap: 2rem;
        gap: 2rem;
    }
    .app__header-schedule-info button{
        width: 100%;
        margin: 0;
    }
    .app__header-text h2 {
        font-size: 5.7rem;
    }
    .app__header-text p.p-text {
        font-size: 2.2rem;
    }
}
@media (max-width: 460px) {


    .app__header-text{
        padding: 7rem 5%;
    }
    .app__header-info{
        width: 100%;
        flex-direction: column;
        align-items: flex-start;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    .app__header-text p.p-text {
        width: 100%;
        margin: 0;
        margin-bottom: 5rem;
    }
    .app__header-img {
        display: none;
    }
}
.app__prep{
    flex-direction: row;
}
.app__prep-img{
    flex: 1 1;
    width: 100%;
    padding-top: 15rem;
}
.app__prep-img img{
    max-width: 100%;
    width: 90%;
    height: auto;
    object-fit: cover;
}
.app__prep-info {
    flex: 1 1;
    padding: 0 10% 0 2%;
}

.app__prep-info p.p-text{
    font-weight: 500;
    font-size: 2.2rem;
}

.app__prep-accordions{
    padding: 2.5rem;
}

.accordion-container{
    width: 100%;
    border: .1rem solid var(--light-blue);
    padding: 1rem;
    margin: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 1rem;
    transition: all .5 ease-in-out;
}

.accordion-header{
    display: flex;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.accordion-header h3{
    color: var(--secondary-blue);
    font-size: 2.4rem;
}
.accordion-content{
    display: none;
    width: 85%;
    margin: 3rem auto;
}
.accordion {
    transition: all .5 ease-in-out;
    cursor: pointer;
}
.accordion.active,
.accordion-container:hover{
    background: rgba( 255, 255, 255, 0.9 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7px );
    -webkit-backdrop-filter: blur( 7px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.accordion.active .accordion-content {
    display: block;
}

.accordion.active .accordion-container{
    border: none;
}

@media (max-width: 460px) {
    .app__prep{
        flex-direction: column!important;
    }
    .app__prep-accordions{
        padding: 0rem;
    }
    .accordion-header h3{
        font-size: 2.2rem;
    }
    .app__prep-img img{
        width: 100%;
    }
}
.section__padding {
  padding: 4rem 6rem;
}

.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.app__container {
  width: 100%;
  min-height: 100vh;

  display: flex;
  flex-direction: row;
}

.app__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  min-height: 100vh;
}
.p-text {
  color: var(--gray-color);
}
.icon-container{
  width: 3rem;
  height: 3rem;
  border-radius: .4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--light-blue);
}

.icon-container{
  color: var(--primary-blue);
}
